@font-face {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('Karla-Regular.woff') format('woff'),
    url('Karla-Regular.woff2') format('woff2');
}

@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url('Karla-Bold.woff') format('woff'),
    url('Karla-Bold.woff2') format('woff2');
}
